@import url(https://fonts.googleapis.com/css2?family=Cormorant+Infant:ital,wght@1,300&display=swap);
body{margin:0;font-family:-apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen', 'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue', sans-serif;-webkit-font-smoothing:antialiased;-moz-osx-font-smoothing:grayscale}code{font-family:source-code-pro, Menlo, Monaco, Consolas, 'Courier New', monospace}

*{margin:0;padding:0}

.svgCake{display:block;position:relative;margin:-10rem auto 0 auto}

.cakeMain{text-align:center;background:#45a575;min-height:calc(100vh + 10rem)}.velas{position:absolute;top:228px;left:50%;margin-left:-20.4px;margin-top:-9.33333333px;transform:translateY(-300px);-webkit-backface-visibility:hidden;backface-visibility:hidden;-webkit-animation:in 500ms 4s ease-out forwards;animation:in 500ms 4s ease-out forwards}.velas h1{color:#ffffff;font-size:2rem}.fuego{border-radius:100%;position:absolute;top:-20px;left:50%;margin-left:-2.6px;width:6.66666667px;height:18px}.fuego:nth-child(1){-webkit-animation:fuego 2s 4.5s infinite;animation:fuego 2s 4.5s infinite}.fuego:nth-child(2){-webkit-animation:fuego 1.5s 4.5s infinite;animation:fuego 1.5s 4.5s infinite}.fuego:nth-child(3){-webkit-animation:fuego 1s 4.5s infinite;animation:fuego 1s 4.5s infinite}.fuego:nth-child(4){-webkit-animation:fuego 0.5s 4.5s infinite;animation:fuego 0.5s 4.5s infinite}.fuego:nth-child(5){-webkit-animation:fuego 0.2s 4.5s infinite;animation:fuego 0.2s 4.5s infinite}.vaweButton{font-family:'Lato';font-weight:600;font-size:1rem;width:250px;height:50px;padding:0.5rem 2rem;margin:2rem;background:transparent;border:none;border-radius:0.2rem;color:white;outline:none;position:relative;overflow:hidden;cursor:pointer}.vaweButton span{position:relative}.vaweButton .wave{position:absolute;top:-100px;left:0;width:250px;height:250px;background:linear-gradient(45deg, #4f00bc, #29abe3);transition:0.5s ease}.vaweButton .wave:after,.vaweButton .wave:before{content:'';position:absolute;width:200%;height:200%;top:0;left:50%;transform:translate(-50%, -75%)}.vaweButton .wave:after{border-radius:40%;background-color:rgba(69,165,117,0.5);-webkit-animation:wave 7s linear infinite;animation:wave 7s linear infinite}.vaweButton .wave:before{border-radius:45%;background-color:#45a575;-webkit-animation:wave 12s linear infinite;animation:wave 12s linear infinite}.vaweButton:hover .wave{top:-150px}@-webkit-keyframes fuego{0%,100%{background:rgba(254,248,97,0.5);box-shadow:0 0 40px 10px rgba(248,233,209,0.2);transform:translateY(0) scale(1)}50%{background:rgba(255,50,0,0.1);box-shadow:0 0 40px 20px rgba(248,233,209,0.2);transform:translateY(20px) scale(0)}}@keyframes fuego{0%,100%{background:rgba(254,248,97,0.5);box-shadow:0 0 40px 10px rgba(248,233,209,0.2);transform:translateY(0) scale(1)}50%{background:rgba(255,50,0,0.1);box-shadow:0 0 40px 20px rgba(248,233,209,0.2);transform:translateY(20px) scale(0)}}@-webkit-keyframes in{to{transform:translateY(0)}}@keyframes in{to{transform:translateY(0)}}@-webkit-keyframes wave{0%{transform:translate(-50%, -75%) rotate(0deg)}100%{transform:translate(-50%, -75%) rotate(360deg)}}@keyframes wave{0%{transform:translate(-50%, -75%) rotate(0deg)}100%{transform:translate(-50%, -75%) rotate(360deg)}}

.bgGreet{background-color:#f6ce84;min-height:100vh;text-align:center;padding-top:5rem}.textGreeting{text-align:center;font-size:5rem;color:#ff0062;font-family:'Cormorant Infant', serif}.balloon{display:inline-block;width:120px;height:145px;background:#799ed2;border-radius:80%;position:relative;box-shadow:inset -10px -10px 0 rgba(0,0,0,0.07);margin:20px 30px;transition:transform 0.5s ease;z-index:10;-webkit-animation:balloons 4s ease-in-out infinite;animation:balloons 4s ease-in-out infinite;transform-origin:bottom center}@-webkit-keyframes balloons{0%,100%{transform:translateY(0) rotate(-4deg)}50%{transform:translateY(-25px) rotate(4deg)}}@keyframes balloons{0%,100%{transform:translateY(0) rotate(-4deg)}50%{transform:translateY(-25px) rotate(4deg)}}.balloon:before{content:'▲';display:block;text-align:center;width:100%;position:absolute;bottom:-12px;z-index:-100}.balloon:after{display:inline-block;top:153px;position:absolute;height:250px;width:1px;margin:0 auto;content:'';background:rgba(0,0,0,0.2)}.balloon:nth-child(2){background:#8882c9;-webkit-animation-duration:3.5s;animation-duration:3.5s}.balloon:nth-child(3){background:#66cc86;-webkit-animation-duration:3s;animation-duration:3s}.balloon:nth-child(4){background:#c9c85e;-webkit-animation-duration:4.5s;animation-duration:4.5s}.balloon:nth-child(5){background:#ce8355;-webkit-animation-duration:5s;animation-duration:5s}.wrapper{margin:5rem auto;width:-webkit-fit-content;width:-moz-fit-content;width:fit-content;height:auto}.button{width:200px;height:70px;background:linear-gradient(to left top, #c32c71 50%, #b33771 50%);border-style:none;color:#fff;font-size:23px;letter-spacing:3px;font-family:'Lato';font-weight:600;outline:none;cursor:pointer;position:relative;padding:0px;overflow:hidden;transition:all 0.5s;box-shadow:0px 1px 2px rgba(0,0,0,0.2)}.button span{position:absolute;display:block}.button span:nth-child(1){height:3px;width:200px;top:0px;left:-200px;background:linear-gradient(to right, rgba(0,0,0,0), #f6e58d);border-top-right-radius:1px;border-bottom-right-radius:1px;-webkit-animation:span1 2s linear infinite;animation:span1 2s linear infinite;-webkit-animation-delay:1s;animation-delay:1s}@-webkit-keyframes span1{0%{left:-200px}100%{left:200px}}@keyframes span1{0%{left:-200px}100%{left:200px}}.button span:nth-child(2){height:70px;width:3px;top:-70px;right:0px;background:linear-gradient(to bottom, rgba(0,0,0,0), #f6e58d);border-bottom-left-radius:1px;border-bottom-right-radius:1px;-webkit-animation:span2 2s linear infinite;animation:span2 2s linear infinite;-webkit-animation-delay:2s;animation-delay:2s}@-webkit-keyframes span2{0%{top:-70px}100%{top:70px}}@keyframes span2{0%{top:-70px}100%{top:70px}}.button span:nth-child(3){height:3px;width:200px;right:-200px;bottom:0px;background:linear-gradient(to left, rgba(0,0,0,0), #f6e58d);border-top-left-radius:1px;border-bottom-left-radius:1px;-webkit-animation:span3 2s linear infinite;animation:span3 2s linear infinite;-webkit-animation-delay:3s;animation-delay:3s}@-webkit-keyframes span3{0%{right:-200px}100%{right:200px}}@keyframes span3{0%{right:-200px}100%{right:200px}}.button span:nth-child(4){height:70px;width:3px;bottom:-70px;left:0px;background:linear-gradient(to top, rgba(0,0,0,0), #f6e58d);border-top-right-radius:1px;border-top-left-radius:1px;-webkit-animation:span4 2s linear infinite;animation:span4 2s linear infinite;-webkit-animation-delay:4s;animation-delay:4s}@-webkit-keyframes span4{0%{bottom:-70px}100%{bottom:70px}}@keyframes span4{0%{bottom:-70px}100%{bottom:70px}}.button:hover{transition:all 0.5s;transform:rotate(-3deg) scale(1.1);box-shadow:0px 3px 5px rgba(0,0,0,0.4)}.button:hover span{-webkit-animation-play-state:paused;animation-play-state:paused}

::selection{background:transparent}.card-bg{background:#865f5f;height:100vh}.card{color:#013243;position:absolute;top:50%;left:50%;width:270px;height:400px;background:#e0e1dc;transform-style:preserve-3d;transform:translate(-50%, -50%) perspective(2000px);box-shadow:inset 300px 0 50px rgba(0,0,0,0.5),20px 0 60px rgba(0,0,0,0.5);transition:1s}.card:hover{transform:translate(-50%, -50%) perspective(2000px) rotate(15deg) scale(1.2);box-shadow:inset 20px 0 50px rgba(0,0,0,0.5),0 10px 100px rgba(0,0,0,0.5)}.card:before{content:'';position:absolute;top:-5px;left:0;width:100%;height:5px;background:#bac1ba;transform-origin:bottom;transform:skewX(-45deg)}.card:after{content:'';position:absolute;top:0;right:-5px;width:5px;height:100%;background:#92a29c;transform-origin:left;transform:skewY(-45deg)}.card .imgBox{width:100%;height:100%;position:relative;transform-origin:left;transition:0.7s}.card .bark{position:absolute;background:#e0e1dc;width:100%;height:100%;opacity:0;transition:0.7s}.card .imgBox img{min-width:250px;max-height:400px}.card:hover .imgBox{transform:rotateY(-135deg)}.card:hover .bark{opacity:1;transition:0.6s;box-shadow:300px 200px 100px rgba(0,0,0,0.4) inset}.card .details{position:absolute;top:0;left:0;box-sizing:border-box;padding:0 0 0 10px;z-index:-1;margin-top:70px}.card .details p{font-size:15px;line-height:15px;transform:rotate(-10deg);padding:0 0 0 20px;font-family:'Cormorant Infant', serif}

